  z-index: 1;
}

.single-banner:hover::before {
  transform: scale(1);
}

.single-banner::before {
  position: absolute;
  left: 30px;
  top: 30px;
  border: 13px solid #be9335;
  content: "";
  opacity: 0.3;
  width: calc(100% - 80px);
  height: calc(100% - 80px);
  transform: scale(0);
  transition: all 0.3s;
}

.single-banner img {
  width: 100%;
  object-fit: cover;
}

